import React from 'react';
import Loadable from 'react-loadable';

const LoadableHelloButton = Loadable({
  loader: () => import('./HelloButton'),
  loading() {
    return <div>Loading...</div>
  }
});

const HelloWorld = () => (
  <div className="hello-world">
    <LoadableHelloButton />
  </div>
);

export default HelloWorld;